import React from 'react';
import NavBar from '../components/layout/navbar';
import Footer from '../components/layout/footer';
import SideBar from '../components/layout/sider';
import RolesCounter from '../components/settings/roles-counter';
import Roles from '../components/settings/roles';
import { BrowserRouter, Routes, Route, useParams } from "react-router-dom";
import { Breadcrumb, Layout, Menu, theme } from 'antd';

const { Header, Content, Sider } = Layout;

const AdminPage = () => {
  const {
    token: { colorBgContainer },
  } = theme.useToken();
  const {token, gameId, creator} = useParams()
  return (
    <Layout>
      <NavBar />
      <Content
        style={{
          padding: '0 50px',
        }}
      >
        <Breadcrumb
          style={{
            margin: '16px 0',
          }}
        >
          <Breadcrumb.Item>Game ID: {gameId}</Breadcrumb.Item>
          <Breadcrumb.Item>Creator: {creator}</Breadcrumb.Item>
          <Breadcrumb.Item>{window.location.pathname.split("/").pop()}</Breadcrumb.Item>
        </Breadcrumb>
        <Layout
          style={{
            padding: '24px 0',
            background: colorBgContainer,
          }}
        >
          <SideBar />
          <Content
            style={{
              padding: '0 24px',
              minHeight: 280,
            }}
          >
            <Routes>
              <Route path="roles" element={<RolesCounter />} />
              <Route path="roles8" element={<Roles />} />
            </Routes>
          </Content>
        </Layout>
      </Content>
      <Footer/>
    </Layout>
  )
}
export default AdminPage;
